import { FC } from 'react';

import { ID_BOOKMARK_BUTTON } from '../Bookmark.constants';
import { BookmarkModalProps } from '../Bookmark.types';

import {
  StyledBookmarkModal,
  Container,
  CloseButton,
  CloseIcon,
} from './BookmarkModal.styles';

import CloseSVG from '#icons/close-solid-16.svg';

const BookmarkModal: FC<BookmarkModalProps> = ({
  isModalOpen,
  closeModal,
  children,
}) => (
  <StyledBookmarkModal
    isHidden={!isModalOpen}
    returnFocus={`#${ID_BOOKMARK_BUTTON}`}
    onEscapeKeyPress={closeModal}
  >
    <Container>
      <CloseButton aria-label="Close" onClick={closeModal}>
        <CloseIcon svg={CloseSVG} />
      </CloseButton>
      {children}
    </Container>
  </StyledBookmarkModal>
);

export default BookmarkModal;
